#chat-header {
  color: #fff;
  background-color: #4caf50;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-weight: bold;
  display: flex;
}

#hide-chatbot {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 16px;
}

#hide-chatbot:hover {
  color: #ddd;
}

#show-chatbot {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 20px;
  padding: 10px 15px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#show-chatbot:hover {
  background-color: #367c39;
}
